.calc-radio {
  flex-basis: 50%; }
  .calc-radio:last-child {
    flex-basis: 100%;
    display: flex; }
  .calc-radio label {
    display: inline-block;
    margin-bottom: 20px; }
    .calc-radio label small {
      display: block;
      margin-left: 28px;
