body,
html {
  background-color: #eef3f5;
  color: #131313;
  font-family: 'Roboto', sans-serif;
  padding-top: 20px;
}

html {
  font-size: 10px;
}

body {
  font-size: 14px;
  margin: 0;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

button:focus,
input:focus,
a:focus,
textarea:focus {
  outline: none;
}

p {
  font-weight: 300;
}

li {
  list-style: none;
}

ul,
ol,
dl {
  margin: 0;
}

a {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  color: #009adf;
  text-decoration: none;
}

h2 {
  font-size: 24px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  margin: 0;
  padding: 0;
}

.btn {
  display: inline-block;
  padding: 20px 30px;
  color: #fff;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 700;
  margin-top: 40px;
}

.btn-primary {
  background-color: #009adf;
  border-bottom: 5px solid #0077ac;
}

.btn-primary:hover {
  background-color: #00a1e9;
}

a:hover {
  text-decoration: none;
}

.top-nav {
  background-color: #fff;
  padding: 15px;
  position: relative;
}

.top-nav_btn {
  display: none;
  width: 30px;
  height: 30px;
  font-size: 18px;
  background-color: transparent;
  border: none;
  position: absolute;
  right: -29px;
  top: 12px;
  -webkit-transform: translate(-50px, -50px);
  -ms-transform: translate(-50px, -50px);
  transform: translate(-50px, -50px);
}

.top-nav_btn:hover {
  cursor: pointer;
}

.top-nav_menu {
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.top-nav_menu li {
  margin-right: 45px;
}

.top-nav_menu li a {
  text-transform: uppercase;
  color: #131313;
  font-weight: 700;
}

.top-nav_menu li a::after {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  background-color: #009adf;
  -webkit-transform: translateY(27px) scale(0);
  -ms-transform: translateY(27px) scale(0);
  transform: translateY(27px) scale(0);
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.top-nav_menu li a:hover::after {
  -webkit-transform: translateY(25px) scale(1);
  -ms-transform: translateY(25px) scale(1);
  transform: translateY(25px) scale(1);
}

.left-sidebar {
  margin-top: 2rem;
}

.left-sidebar_btn {
  background-color: #fff;
  padding: 15px;
  position: relative;
  margin-bottom: 2rem;
  display: none;
}

.left-sidebar_btn::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 50%;
  margin-top: -10px;
  right: 15px;
  width: 15px;
  height: 15px;
  border-bottom: 2px solid #131313;
  border-right: 2px solid #131313;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.left-sidebar_btn:hover {
  cursor: pointer;
}

.left-sidebar_menu {
  background-color: #fff;
  padding: 0;
}

.left-sidebar_menu li {
  -webkit-transition: all 0.1s;
  -o-transition: all 0.1s;
  transition: all 0.1s;
}

.left-sidebar_menu li:first-child a {
  padding-top: 20px;
}

.left-sidebar_menu li:last-child a {
  padding-bottom: 20px;
}

.left-sidebar_menu li:hover {
  background-color: #009adf;
}

.left-sidebar_menu li:hover a {
  color: #fff;
}

.left-sidebar_menu li a {
  display: block;
  color: #131313;
  font-size: 16px;
  font-weight: 400;
  padding: 15px 29px;
}

.left-sidebar_object {
  background-color: #fff;
  margin-top: 3rem;
}

.left-sidebar_object-description {
  padding: 30px;
}

.left-sidebar_object-description h3 {
  text-transform: uppercase;
  font-weight: 400;
}

.content {
  margin-top: 40px;
  margin-bottom: 76px;
}

.content h2 {
  font-size: 24px;
}

.directions {
  margin-top: 80px;
}

.directions h2 {
  margin-bottom: 20px;
}

.directions-block {
  background-color: #fff;
  height: 220px;
  text-align: center;
  padding: 20px;
}

.directions-block_bg {
  width: 100%;
  height: 100%;
  background-position: bottom center;
  background-repeat: no-repeat;
}

.directions-block h3 {
  color: #009adf;
  font-size: 14px;
}

.slick-slide {
  margin: 0 15px;
}

.slick-active {
  outline: none !important;
}

.footer {
  margin-top: 60px;
  background-color: #131313;
  color: #666666;
  padding: 59px 0;
}

.footer-policy-link {
  font-size: 12px;
}

.footer p {
  margin-bottom: 14px;
}

.footer a {
  color: #666666;
}

.footer a:hover {
  color: #fff;
}

.footer small {
  display: block;
  margin-top: 56px;
  line-height: 16px;
  font-size: 10px;
  max-width: 80%;
}

.footer ul {
  padding: 0;
}

.footer ul li {
  line-height: 32px;
}

.footer-contacts,
.footer-social-links {
  text-align: right;
}

.footer-contacts_phone {
  font-size: 20px;
  color: #009adf;
}

.footer-social-links li {
  font-size: 20px;
  padding-top: 2px;
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.1);
  color: #131313;
  text-align: center;
  vertical-align: middle;
  line-height: 32;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  margin-left: 5px;
  -webkit-transition: 0.2 all;
  -o-transition: 0.2 all;
  transition: 0.2 all;
}

.footer-social-links li:hover {
  background-color: rgba(255, 255, 255, 0.9);
  color: #131313;
}

.footer-social-links li:hover a {
  color: #131313;
}

.team {
  text-align: center;
  padding-top: 20px;
}

.team h4 {
  font-size: 18px;
  font-weight: 400;
  margin-top: 15px;
}

.team p {
  margin-bottom: 40px;
}

.contacts {
  font-size: 16px;
}

.contacts .fa {
  font-size: 25px;
  margin-right: 12px;
  color: #009adf;
  vertical-align: middle;
}

.contacts-address {
  display: block;
  line-height: 2;
}

.contacts-btn {
  margin-top: 36px;
  margin-bottom: 45px;
}

.contacts-social-links ul {
  padding: 0;
}

.contacts-social-links ul li {
  display: inline-block;
}

.contacts-social-links ul li a .fa {
  font-size: 20px;
  display: inline-block;
  color: #fff !important;
  background-color: #009adf;
  margin-right: 15px;
  padding-top: 1px;
  width: 32px;
  height: 32px;
  text-align: center;
  line-height: 32px;
  border-radius: 50%;
  vertical-align: middle;
}

.organization-card {
  background-color: #fff;
  padding: 25px 30px;
  margin-bottom: 30px;
  min-height: 42rem;
  -webkit-transition: auto -0.2s;
  -o-transition: auto -0.2s;
  transition: auto -0.2s;
}

.organization-card:hover {
  -webkit-box-shadow: 0 5px 10px rgba(0, 0, 1, 0.1);
  box-shadow: 0 5px 10px rgba(0, 0, 1, 0.1);
}

.organization-card a {
  text-transform: uppercase;
  display: block;
  margin-bottom: 20px;
}

.organization-card small {
  display: block;
  color: rgba(19, 19, 19, 0.5);
  line-height: 1.5;
}

.organization-card p {
  line-height: 22px;
}

.calc h4 {
  margin-top: 0;
  margin-bottom: 30px;
}

.calc-programms {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.calc-radio {
  -ms-flex-preferred-size: 50%;
  flex-basis: 50%;
}

.calc-radio:last-child {
  -ms-flex-preferred-size: 100%;
  flex-basis: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.calc-radio label {
  display: inline-block;
  margin-bottom: 20px;
}

.calc-radio label small {
  display: block;
  margin-left: 28px;
  font-size: 10px;
  opacity: 0.5;
}

.calc-radio label:hover {
  cursor: pointer;
}

.calc-radio label::before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  width: 24px;
  height: 24px;
  -webkit-box-shadow: 0 5px 10px rgba(0, 0, 1, 0.1);
  box-shadow: 0 5px 10px rgba(0, 0, 1, 0.1);
  background-color: #fff;
  border: 6px solid #fff;
  border-radius: 50%;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.calc-radio input[type="radio"] {
  display: none;
}

.calc-radio input[type="radio"]:checked + label::before {
  background-color: #009adf;
  border: 6px solid #fff;
}

.calc-popular {
  padding-left: 5px;
  padding-right: 5px;
  margin-left: 16px;
  height: 25px;
  color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  font-size: 10px;
  text-transform: uppercase;
  background-color: #009adf;
  position: relative;
}

.calc-popular::before {
  content: '';
  width: 6px;
  height: 13px;
  position: absolute;
  top: 50%;
  left: -5px;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  border: 6px solid transparent;
  border-right: 6px solid #009adf;
}

.calc-legal {
  font-size: 12px;
}

.calc-summ_invest {
  margin-top: 30px;
}

.calc-summ_invest_label {
  margin-right: 10px;
}

.calc-summ_invest_num {
  display: inline-block;
  padding: 15px 48px;
  background-color: #fff;
  font-size: 18px;
}

.calc-total,
.calc-monthly {
  margin-top: 20px;
  display: inline-block;
  padding: 10px;
  background-color: #fff;
  text-align: center;
  margin-right: 40px;
}

.calc-total strong,
.calc-monthly strong {
  color: #009adf;
  font-size: 18px;
  font-weight: 400;
}

.calc-total small,
.calc-monthly small {
  font-size: 10px;
}

.calc-monthly {
  background-color: transparent;
  font-size: 18px;
}

.object-card {
  background-color: #fff;
  margin-bottom: 30px;
}

.object-card img {
  width: 100%;
}

.object-card_text {
  padding: 30px;
}

.object-card_title {
  display: block;
  font-size: 16px;
  color: #009adf;
}

.object-card_price {
  display: block;
  font-size: 20px;
  text-align: center;
  color: #009adf;
  font-weight: 700;
}

.object-card_btn {
  display: block;
  text-align: center;
}

.object-card_more {
  display: block;
  color: #131313;
  margin-top: 30px;
  text-align: center;
}

.object-page_gallery {
  float: right;
}

.modal {
  display: none;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  -webkit-box-shadow: 0 12px 24px rgba(0, 0, 0, 0.18);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.18);
}

.modal-content {
  position: relative;
  background-color: #fff;
  width: 100%;
  max-width: 370px;
  margin: 50px auto 0;
  text-align: center;
  padding: 30px 50px;
}

.modal-content .close {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  right: 15px;
  top: 15px;
  opacity: 0.5;
  font-size: 20px;
}

.modal-content .close:hover {
  cursor: pointer;
}

.modal-content h4 {
  font-size: 18px;
  margin-top: 0;
  margin-bottom: 14px;
  color: #009adf;
}

.modal-content p {
  font-size: 12px;
  margin-top: 0;
}

.modal-form {
  margin-top: 37px;
}

.modal-form input {
  display: block;
  width: 100%;
  border: 1px solid rgba(19, 19, 19, 0.15);
  border-radius: 4px;
  padding: 20px;
  font-size: 15px;
  margin-bottom: 10px;
}

.modal-form_btn {
  display: block;
  width: 100%;
  margin-bottom: 20px;
}

@media (min-width: 1199.98px) {
  .directions-block_bg {
    background-size: 95%;
  }
}

@media (max-width: 991.98px) {
  .left-sidebar_object {
    display: none;
  }

  .slick-slide {
    margin: 0 5px;
  }
}

@media (max-width: 767.98px) {
  html {
    font-size: 7px;
  }

  .top-nav_btn {
    display: block;
  }

  .top-nav_menu {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    display: none;
  }

  .top-nav_menu__active {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 0;
    margin-top: 20px;
  }

  .top-nav_menu li {
    line-height: 2;
  }

  .top-nav_menu li a::after {
    display: none;
  }

  .left-sidebar_btn {
    display: block;
  }

  .left-sidebar_menu {
    display: none;
  }

  .left-sidebar_menu__active {
    display: block;
  }

  .footer .copy {
    text-align: center;
  }

  .footer small {
    margin: 36px auto;
  }

  .footer-contacts,
  .footer-social-links {
    text-align: left;
  }
}

@media (min-width: 767.98px) and (max-width: 991.98px) {
  .top-nav_btn {
    display: block;
  }

  .top-nav_menu {
    display: none;
  }

  .top-nav_menu__active {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 0;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    margin-top: 20px;
  }

  .top-nav_menu li {
    margin-right: 0px;
  }

  .left-sidebar_btn {
    display: block;
  }

  .left-sidebar_menu {
    display: none;
  }

  .left-sidebar_menu__active {
    display: block;
  }

  .footer .copy {
    text-align: center;
  }

  .footer small {
    margin: 36px auto;
  }

  .footer ul {
    text-align: center;
  }

  .footer-contacts,
  .footer-social-links {
    text-align: center;
  }
}

@media (min-width: 991.98px) and (max-width: 1199.98px) {
  .top-nav_menu li a {
    font-size: 11px;
  }
}